import { Component, OnInit, OnDestroy, inject } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MenuItem } from 'primeng/api';
import { LineaTarea } from 'src/app/interfaces/tarea.interface';
import { TareaService } from 'src/app/services/tarea.service';

@Component({
  selector: 'app-mostrar-tarea',
  templateUrl: './mostrar-tarea.component.html',
  styleUrls: ['./mostrar-tarea.component.scss']
})
export class MostrarTareaComponent implements OnInit, OnDestroy {

    private readonly tareaService = inject(TareaService);
    private readonly activatedRoute = inject(ActivatedRoute);
    
    cardMenu: MenuItem[];

    tarea: LineaTarea;

    subcripciones = []

    ngOnInit(): void {

        this.subcripciones.push(
            this.activatedRoute.params.subscribe(
                (params) => {
                    this.subcripciones.push(
                        // @ts-ignore
                        this.tareaService.getLineaTarea(params.id).subscribe(
                            (response) => {
                                console.log(response);
                                this.tarea = response;
                            },
                            (error) => {
                                console.log(error);
                            }
                        )
                    )
                }
            )
        )

        // this.cardMenu = [
        //     {
        //         label: 'Save', icon: 'pi pi-fw pi-check'
        //     },
        //     {
        //         label: 'Update', icon: 'pi pi-fw pi-refresh'
        //     },
        //     {
        //         label: 'Delete', icon: 'pi pi-fw pi-trash'
        //     },
        // ];
    }

    ngOnDestroy(): void {
        this.subcripciones.forEach(
            (subcripcion) => {
                subcripcion.unsubscribe()
            }
        )
    }
}
